<template>
	<div class="address">
		<van-nav-bar title="地址栏" fixed left-text="返回" left-arrow @click-left="goback" />
			<AddList >
				<van-address-list v-model="chosenAddressId" 
				:list="list" 
				@add="add"
				:switchable="false"
				@edit="edit"
				default-tag-text="默认" />
			</AddList>
	</div>
</template>

<script>
	import "../assets/less/Address.less"
	import AddList from '../components/AddList.vue'
	export default {
		name: "Address",
		data() {
			return {
				chosenAddressId: '1',
				list: [],
				tokenString:""
				
			}
		},
		components: {
			AddList,
		},
		computed: {
		
		},
		created() {
			this.getAddRess()
		},
			
		methods: {
			//跳转新增地址页面
			add(){
				this.$router.push({name:"AddNewRess"})
			},
			//返回上一级
			goback(){
				this.$router.go(-1)
			},
			//查询地址接口
			getAddRess(){
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					console.log("没有token值");
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'GET',
					url: '/findAddress',
					params:{
						tokenString:this.tokenString
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 700) {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						});
						return this.$router.push({
							name: "Login"
						})
				
					}
					// console.log(res);
					if (res.data.code == 20000){
						res.data.result.forEach(v=>{
							v.address=`${v.province}${v.city}${v.county}${v.addressDetail}`
							v.isDefault=Boolean(v.isDefault)
						})
						this.list=res.data.result
					}
					
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//编辑地址
			edit({aid}){
				this.$router.push({name: 'AddNewRess', params: {aid}});
			}
			
		},
		

	}
</script>
